import { Component } from '@angular/core';
import { TranslateService } from "@ngx-translate/core";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'myBlogDev';
  arrLang = ['en-US', 'zh-Hans', 'zh-Hant'];

  currentLang: string = 'zh-Hans';

  constructor(

    public translate: TranslateService
  ) {
    const arrLang = ['en-US', 'zh-Hans', 'zh-Hant'];


    translate.addLangs(arrLang);
    translate.setDefaultLang('zh-Hans');

    this.initLang(translate).then(lang => {
      this.currentLang = 'zh-Hans';
    });
  }


  /**
   * 初始化语言
   */
   async initLang(translate) {
    const lang = 'zh-Hans';

    if (this.arrLang.includes(lang)) {
      await this.setLang(translate, lang);
    } else {
      await this.setLang(translate, 'zh-Hans');
    }
  }

 
   /**
   * 设置语言
   */
    async setLang(translate, lang) {
      if (this.arrLang.includes(lang)) {
        //translate.setDefaultLang(lang);
        translate.use(lang);
        return true;
      }
      return false;
    }
}
